<template>
	<!--系统管理-->
	<div class="d-box v-box">
		<div class="headDiv">
			<div class="headerLeft">
				<span class="system">关键字搜索</span>
				<el-input placeholder="请输入内容" v-model="name" clearable>
				</el-input>
			</div>

			<div class="clearfix">
				<div style="float: right;">
					<SolidButton :type='"solid"' :text='"查询"' @click="see"></SolidButton>
					<SolidButton :type='"hollow"' :text='"重置"' @click="reset"></SolidButton>
				</div>
			</div>
			<div class="clearfix" style="padding-top: 20px;height: auto;">
				<Button :type='add.type' :icon='add.icon' :text='add.text' @click="addClick"></Button>
			</div>
		</div>
		<div class="flexTable">
			<div class="d-box v-box">
				<div class="flexTable">
					<div class="tableDiv">
						<div class="problem" v-for="o in rows">
							<div class="problemT">
								<div class="problemTop">
									<span>问题</span>
									<ul>
										<li v-for="t in o.questions" v-text="t"></li>
									</ul>
								</div>
								<div class="problemBotton">
									<span>答案:</span>
									<ul>
										<li v-text="o.replyAnswer"></li>
									</ul>
								</div>
							</div>

							<div class="problemB">
								<div class="operation">
									<button class="icon-edit icon" @click="editClick(o)"></button>
									<button class="icon-delete icon" @click="deleteClick(o.replyType)"></button>
								</div>
								<div class="time">
									<span class="timeP">创建于:</span>
									<span class="timeP" v-text="o.createTime"></span>
								</div>
							</div>

						</div>
					</div>
				</div>
				<div class="footerDiv">
					<el-pagination style="text-align: center;padding: 6px 0px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage1" :page-size="10" layout="total, prev, pager, next, jumper" :total="num">
					</el-pagination>
				</div>
			</div>
		</div>

		<el-dialog :title="title" :visible.sync="plus" width='600px' style='text-align: center;'>
			<div class="content">
				<div class="contentTop">
					<div class="contentLeft">问题</div>
					<div class="contentRight">
						<div v-for="(o,index) in list" class="contentInput">
							<input :id="'input'+index" v-model="o.name" v-on:input="changeValue" />
							<span class="off" :id="'off'+index" @click="off(index)">x</span>
						</div>
						<div v-show="flag" class="add el-icon-plus" @click="addPush"></div>
					</div>
				</div>
				<div class="contentBotton">
					<div class="contentLeft">回答</div>
					<textarea v-model="replyAnswer"></textarea>
				</div>
			</div>
			<div slot="footer" class="dialog-footer">
				<SolidButton :type='"hollow"' :text='"取消"' @click="plus = false"></SolidButton>
				<SolidButton :type='"solid"' :text='"确认"' @click="confirm"></SolidButton>
			</div>
		</el-dialog>
	</div>

</template>

<script>
	import Button from '../../button/Button.vue';
	import SolidButton from '../../SelfControls/solidButton/solidButton.vue';

	export default {
		components: {
			Button,
			SolidButton
		},
		data() {
			return {
				num: 0, //jxs会报一个类型错误，就设了一个数字初始值
				currentPage1: 1,
				rows: [],
				name: '',
				plus: false,
				title: '',
				ids: '',
				followed: '',
				pageNum: '1',
				pageRows: '10',
				formLabelWidth: '77px',
				add: {
					text: '新增',
					type: 'icon-add',
					icon: 'el-icon-plus',
				},
				list: [{
					name: ''
				}],
				questions: [],
				replyAnswer: '',
				queslist: [],
				flag: false,
				replyType: ''
			}
		},
		methods: {
			//change
			changeValue() {
				if(this.list.length == 10) {
					this.flag = false
					return
				}
				for(var i = 0; i < this.list.length; i++) {
					if(this.list[i].name == '') {
						this.flag = false
						return
					} else {
						this.flag = true
					}
				}
			},
			//删除单挑问题
			off(index) {
				if(this.list.length != 1) {
					this.list.splice(index, 1);
				} else {
					this.$message({
						message: '不能删除',
						type: 'warning'
					});
				}
				for(var i = 0; i < this.list.length; i++) {
					if(this.list[i].name == '') {
						this.flag = false
						return
					} else {
						this.flag = true
					}
				}

			},
			//添加
			addPush() {
				this.flag = false
				this.list.push({
					name: ''
				})
			},
			//新增
			addClick() {
				this.flag = false
				this.title = '新增';
				this.list = [{
					name: ''
				}]
				this.plus = true;
				this.replyAnswer = ''
			},
			//删除
			deleteClick(o) {
				var self = this
				this.$api.post('crm/wxReply/delete', o, r => {
					self.loadData();
				}, e => {
					console.log(e);
				})
			},
			//编辑
			editClick(o) {
				var self = this
				this.title = '编辑';
				this.list = []
				this.plus = true;
				this.replyAnswer = o.replyAnswer;
				this.replyType = o.replyType
				for(var i = 0; i < o.questions.length; i++) {
					self.list.push({
						name: o.questions[i]
					})
				}
			},
			//初始表格查询
			loadData() {
				var self = this;
				var p = {
					replyQuestion: self.name,
					type: "1",
					pageNum: self.pageNum,
					pageRows: self.pageRows,
					operateCode: '100',
					operateName: 'test',
				}
				self.$api.post('crm/wxReply/getReplyList', p, r => {
					self.rows = r.repData.list
					self.num = r.repData.total
				}, e => {
					this.$message({
						message: e.repData.repMsg,
						type: 'error'
					});
				})
			},
			//分页
			handleSizeChange(val) {
				this.pageRows = val;
				this.loadData();
			},
			handleCurrentChange(val) {
				this.pageNum = val;
				this.loadData();
			},
			//查询
			see() {
				this.loadData();
			},
			//重置
			reset() {
				this.name = '';
				this.loadData();
			},
			//新增确认
			confirm() {
				var self = this;
				this.questions = [];
				for(var i = 0; i < this.list.length; i++) {
					this.questions.push(document.getElementById('input' + i).value)
				}
				if(this.questions != '') {
					for(var i = 0; i < this.questions.length; i++) {
						if(this.questions[i] == '') {
							this.$message({
								message: '请填写全部问题',
								type: 'warning'
							});
							return false
						}
					}
					if(this.replyAnswer == '') {
						this.$message({
							message: '请填写回答',
							type: 'warning'
						});
						return false
					}
					this.$api.post('crm/wxReply/insertOrUpdate', {
						questions: this.questions,
						replyAnswer: this.replyAnswer,
						replyType: this.replyType
					}, r => {
						this.plus = false;
						self.loadData();
					}, e => {
						console.log(e);
					})
				}
			}
		},
		created: function() {
			this.loadData();
		},
	}
</script>

<style scoped="scoped">
	#off0 {
		display: none;
	}
	
	.content {
		width: 100%;
		min-height: 200px;
	}
	
	.contentTop {
		width: 100%;
		min-height: 100px;
		overflow: hidden;
	}
	
	.contentBotton {
		width: 100%;
		min-height: 100px;
	}
	
	.contentBotton textarea {
		width: 74%;
		min-height: 100px;
		float: right;
		resize: none;
		padding: 10px;
	}
	
	.contentLeft {
		display: inline-block;
		width: 20%;
		height: 100%;
		float: left;
		line-height: 42px;
	}
	
	.contentRight {
		width: 80%;
		min-height: 100px;
		float: right;
	}
	
	.contentInput {
		width: 45%;
		float: left;
		margin: 5px 10px;
		position: relative;
	}
	
	.off {
		display: inline-block;
		width: 10px;
		height: 10px;
		font-size: 12px;
		text-align: center;
		line-height: 10px;
		position: absolute;
		top: 2px;
		right: 2px;
		cursor: pointer
	}
	
	.contentRight input {
		float: left;
		border: 1px solid #ECEDF2;
		width: 100%;
		height: 30px;
		border-radius: 5px;
		text-indent: 10px;
	}
	
	.add {
		width: 30px;
		height: 30px;
		float: left;
		color: #DE6765;
		border: 2px solid #DE6765;
		border-radius: 50%;
		margin: 5px 10px;
		text-align: center;
		line-height: 30px;
		font-size: 20px;
		font-weight: 900;
	}
	
	.icon {
		background: #fff;
		border: 0;
		width: 50px;
		height: 50px;
		cursor: pointer
	}
	
	.icon:hover:before {
		color: #D86A9C
	}
	
	.icon:before {
		color: #999;
		font-weight: 900;
	}
	
	.problem {
		width: 98%;
		min-height: 100px;
		margin-left: 1%;
		border-radius: 10px;
		border: 1px solid #F5F5F5;
		float: left;
		margin-top: 10px;
	}
	
	.problemTop {
		width: 70%;
		min-height: 30px;
		border-radius: 10px 10px 0 0;
		float: left;
	}
	
	.problemBotton {
		width: 70%;
		min-height: 40px;
		border-radius: 0 0 10px 10px;
		float: left;
	}
	
	.operation {
		width: 100%;
	}
	
	#arrProblem {
		color: #F4516B;
		background: #fff;
		border: 1px solid #F4516B;
	}
	
	.problemTop span,
	.problemBotton span {
		display: inline-block;
		height: 100%;
		width: 10%;
		text-align: center;
		line-height: 40px;
		float: left;
	}
	
	.problemTop span {
		line-height: 30px;
	}
	
	.problemTop ul,
	.problemBotton ul {
		width: 90%;
		height: 100%;
		float: right;
	}
	
	.problemBotton ul li {
		padding: 10px 0;
		line-height: 24px;
	}
	
	.problemTop ul {
		padding-top: 2px;
	}
	
	.problemTop ul li {
		font-size: 12px;
		line-height: 25px;
		display: inline-block;
		margin: 3px;
		padding: 0 15px;
		border-radius: 30px;
		background: #F4516B;
		color: #fff;
	}
	
	.problemT {
		width: 70%;
		min-height: 100px;
		float: left;
	}
	
	.problemB {
		width: 30%;
		min-height: 100px;
		float: right;
	}
	.time{
		width: 96%;
		padding-left: 4%;
	}
	.timeP{
		line-height: 50px;
		color: #666;
	}
	/*head-left内容*/
	
	.headerLeft {
		width: 294px;
		height: 40px;
		float: left;
	}
	
	.headerLeft span {
		width: 85px;
	}
	
	.headerLeft .el-input {
		width: 188px;
		height: 37px;
	}
	
	.system {
		display: inline-block;
		width: 100px;
		line-height: 40px;
		text-align: center;
	}
	
	.right {
		display: inline-block;
		margin-left: 20px;
		float: right;
	}
	
	.dialog-footer {
		text-align: center;
	}
	
	.d-box {
		display: -moz-box;
		/* OLD - Firefox 19- (buggy but mostly works) */
		display: -webkit-box;
		/* OLD - iOS 6-, Safari 3.1-6 */
		display: -webkit-flex;
		/* NEW - Chrome */
		display: -ms-flexbox;
		/* TWEENER - IE 10 */
		display: box;
		display: flexbox;
		display: flex;
		/* NEW, Spec - Opera 12.1, Firefox 20+ */
		height: 100%;
		width: 100%;
	}
	
	.v-box {
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-moz-box-orient: vertical;
		-moz-box-direction: normal;
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}
	
	.headDiv {
		padding: 20px;
		background: #fff;
		overflow: hidden;
		position: relative;
	}
	
	.flexTable {
		-webkit-box-flex: 1;
		-moz-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
		position: relative;
	}
	
	.tableDiv {
		width: 100%;
		height: 100%;
		position: absolute;
		overflow: auto;
	}
	
	.footerDiv {
		height: 40px;
		position: relative;
	}
</style>


